@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
/* menu css  */
nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100px;
  background-color: #0080FF;
  color: #fff;
 
}

.nav-links{
  width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
 
}

.nav-li{
  text-decoration: none;
  color: #fff;
}

.nav-li:hover{
  text-decoration: none;
  color: #fff;
}

/* menu css  end */

/* footer start */

footer{
  display: flex;
  justify-content: space-around;
  padding: 40px;
  background-color: #1E1E1E;
}

.footer{
 margin-right: 15px;
  color: #fff;
  opacity: 0.5;
}

.footer-para{
  color: #fff;
  opacity: 0.5;
}

.footer-icons{
  height: 35px;
  margin-right: 15px;
}

.footer-images{
  width: 25%;
  margin-right: 15px;
}

/* footer end */

/* home page start */

.home-section{
  margin-bottom: 80px;
}

.background{
  background-color: #1E1E1E;
  color: #fff;
  margin-bottom: 20px;
 
}
.home-heading{
  margin-left: 150px;
}

.reel-section{
  display: flex;
}

.reel-image{
  width: 30%;
}

/* home end */

/* movies start */

.movie{
  display: flex;
  margin-bottom: 50px;
}

.movie-images{
 height: 250px;
 margin-right: 10px;
}
